import { Button, Stack, Nav, Navbar, NavDropdown, Form, InputGroup, Container, Row, Col } from 'react-bootstrap';
import '../css/layout.css';
import '../css/footer.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import logo from '../images/logo.png';
function Footer() {
    return(
        <footer className="bg-body-tertiary">
            <Container>
                <Nav
                    activeKey="/home"
                    onSelect={(selectedKey) => alert(`selected ${selectedKey}`)}
                    className="flex-content-center footer-nav">
                    <Nav.Item>
                        <Nav.Link href="/home">
                            
                        </Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link eventKey="link-1">
                            
                        </Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link eventKey="link-2">
                            
                        </Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link eventKey="disabled">
                            
                        </Nav.Link>
                    </Nav.Item>
                </Nav>
                <Row>
                    <Col xs={3}>
                        <img src={logo} alt=""/>
                    </Col>
                    <Col xs={9} className="pt50px">
                        <p>
                               12-1<br/>
                             : <br/>
                             : 1234-1232-1234<br/>
                            Copyright GC COMPANY Corp. All rights reserved.<br/>
                        </p>
                    </Col>
                </Row>
            </Container>
        </footer>
    )
}

export default Footer;